import React, { useState } from "react";
import { Layout, Menu, Divider } from "antd";
import {
  AlignCenterOutlined,
  LineChartOutlined,
  DatabaseOutlined,
  FileOutlined,
  ApiTwoTone
} from "@ant-design/icons";
import { Link, useLocation } from "react-router-dom";


const { Sider } = Layout;

function SideBar() {
  const location = useLocation();
  const [collapsed, setCollapsed] = useState(false)

  const selectedMenu = location.pathname.split("/")[1];



  return (
    <Sider
      collapsible={true}
      collapsedWidth="50"
      theme="light"
      collapsed={collapsed}
      onCollapse={(collapsed, type) => {
        setCollapsed(collapsed)

      }}
    >
      <div className="logo-container">
        <img className={collapsed ? "logo-small" : "logo-large"} src={collapsed ? "/img/o-logo.png" : "/img/black-logo.png"} alt=""></img>
      </div>
      <Menu
        style={{ marginTop: 10 }}
        mode="inline"
        defaultSelectedKeys={[selectedMenu]}
      >
        <Menu.Item key="" icon={<LineChartOutlined />}>
          <Link to="/">Schema Flow</Link>
        </Menu.Item>
        <Menu.Item key="collection" icon={<AlignCenterOutlined />}>
          <Link to="/collections">Collections</Link>
        </Menu.Item>
        <Menu.Item key="data" icon={<DatabaseOutlined />}>
          <Link to="/data">Data Flow</Link>
        </Menu.Item>

        <Divider orientation="left">
          {collapsed ? < ApiTwoTone /> : <b>Plugins</b>}
        </Divider>
        <Menu.Item key="files" icon={<FileOutlined />}>
          <Link to="/files">Files</Link>
        </Menu.Item>
      </Menu>
    </Sider>
  );
}

export default SideBar;
