import React, { useState, useRef, useEffect } from "react";
import { Modal, Button, Input, message, Checkbox, Tag, Space } from "antd";
import { PlusOutlined } from "@ant-design/icons";
import { useDispatch } from "react-redux";
import { Dispatch } from "redux";
import { v4 as uuidv4 } from "uuid";

import { addCollection, selectCollection } from "../store/actionCreators";

import { ICollection, ICrudOperations } from "../types/global";

import RequiredText from "./common/RequiredText";

function NewCollectionModal({
  collections,
  isModalVisible,
  setIsModalVisible,
}: {
  collections: Array<ICollection>;
  isModalVisible: boolean;
  setIsModalVisible: Function;
}) {
  const [collectionName, setCollectionName] = useState("");
  const [showData, setShowData] = useState(true);
  const [crudOperation, setCrudOperation] = useState<ICrudOperations>({
    list: true,
    create: true,
    read: true,
    update: true,
    delete: true,
    deleteAll: true,
  });
  const nameInput = useRef<any>();
  const dispatch: Dispatch<any> = useDispatch();

  const newCollection = React.useCallback(
    (collection: ICollection) => dispatch(addCollection(collection)),
    [dispatch]
  );

  const setSelectedCollection = React.useCallback(
    (collection: ICollection) => dispatch(selectCollection(collection)),
    [dispatch]
  );

  const showModal = () => {
    setIsModalVisible(true);
  };

  const error = () => {
    message.error("Collection already exists");
  };

  const handleOk = () => {
    if (collectionName.length === 0) return;
    if (collections.find((collection) => collection.name === collectionName)) {
      error();
      return;
    }

    const col: ICollection = {
      id: uuidv4(),
      name: collectionName,
      schema: {},
      fields: 0,
      show: showData,
      operations: crudOperation,
      x: Math.floor(collections.length % 4) * 300 + 20,
      y: (Math.floor(collections.length / 4) + 1) * 60,
    };
    newCollection(col);
    setSelectedCollection(col);
    setCollectionName("");
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  useEffect(() => {
    if (nameInput.current) {
      nameInput.current.focus();
    }
  }, []);
  return (
    <>
      <Button type="primary" shape="round" onClick={showModal}>
        Create New
      </Button>
      <Modal
        title={
          <span>
            {" "}
            <PlusOutlined /> Create new collection
          </span>
        }
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
      >
        <Space direction="vertical">
          <Input
            ref={nameInput}
            placeholder="Collection Name"
            value={collectionName}
            onChange={({ target: { value } }) => {
              if (/[^a-zA-Z]/.test(value)) return;
              setCollectionName(value);
            }}
            onKeyDown={(e) => {
              if (e.key === "Enter") {
                handleOk();
              }
            }}
          />
          {collectionName.length === 0 && <RequiredText />}

          <Checkbox onChange={() => setShowData(!showData)} value={showData}>
            {" "}
            <b>Show on data flow ?</b>
          </Checkbox>

          <Tag color="geekblue" >Allowed Operations</Tag>
          <div>
            {Object.keys(crudOperation).map((key) => (
              <Checkbox
                onChange={() =>
                  setCrudOperation({
                    ...crudOperation,
                    [key]: !crudOperation[key],
                  })
                }
                value={crudOperation[key]}
              >
                {" "}
                <b>{key}</b>
              </Checkbox>
            ))}
          </div>
        </Space>
      </Modal>
    </>
  );
}

export default NewCollectionModal;
