import { Space, InputNumber, Input, Checkbox } from "antd";
import { ICollectionSchema } from "../../types/global";
import { CheckboxValueType } from "antd/lib/checkbox/Group";
import { useState } from "react"

function Number({ state, setState }: { state: ICollectionSchema & { name: string }, setState: Function }) {

  const [typeSettings, setTypeSettings] = useState<Array<CheckboxValueType>>([]);



  const updateState = (key: string, value: any) => {
    const updatedData: any = {}
    updatedData[key] = value
    setState((prev: ICollectionSchema) => ({ ...prev, ...updatedData }))
  }

  return (
    <>{
      state.type === "number" &&
      <Space direction="vertical" style={{ width: "100%" }}>
        <span>Name for the type</span>
        <Input placeholder="Type Name" onChange={(e) => updateState("name", e.target.value)} />
        <span>Default value</span>
        <InputNumber
          min={1}
          max={10}
          onChange={(e) => updateState("default", e)}
          placeholder={"10"}
          style={{ width: "100%" }}
        />

        <span>Settings</span>
        <Checkbox.Group
          style={{ width: "100%" }}
          onChange={(e) => {
            const settings: any = {
              index: false,
              required: false,
              unique: false,
            }

            setState((prev: ICollectionSchema) => {
              Object.keys(settings).forEach(key => {
                if (e.includes(key)) {
                  settings[key] = true
                }
              })
              return { ...prev, ...settings }

            })
            setTypeSettings(e)
          }}
          value={typeSettings}
        >
          <Space direction="vertical">
            <Checkbox value="required">Required</Checkbox>
            <Checkbox value="index">Index</Checkbox>
            <Checkbox value="unique">Unique</Checkbox>
            <Checkbox value="minLength">Minimum Length</Checkbox>
            {typeSettings.includes("minLength") && (
              <InputNumber
                min={1}
                onChange={(number) => updateState("minLength", number)}
              />
            )}
            <Checkbox value="maxLength">Maximum Length</Checkbox>
            {typeSettings.includes("maxLength") && (
              <InputNumber
                min={1}
                onChange={(number) => updateState("maxLength", number)}
              />
            )}
          </Space>
        </Checkbox.Group>
      </Space>
    }</>
  );
}

export default Number;
