import { useEffect, useState } from "react"
import { InboxOutlined } from "@ant-design/icons";
import { Form } from "antd";
import FilesView from "../../components/File/FilesView";



function UploadFile({ schemaValue, form }: { schemaValue: any, form: any }) {

  const [selectedFiles, setSelectedFiles] = useState([])

  useEffect(() => {
    form.setFieldsValue({
      [schemaValue.name]: selectedFiles,
    })
  }, [selectedFiles])

  return (
    <>
      <Form.Item
        label={
          <span
            style={{
              textTransform: "capitalize",
              color: "#434343",
            }}
          >
            {schemaValue.name}
          </span>
        }
        name={schemaValue.name}
        rules={[
          {
            required: schemaValue.required,
          },
        ]}
      >

        <div >

          <FilesView setCustomFiles={setSelectedFiles} />
        </div>


      </Form.Item>
    </>
  );
}

export default UploadFile;
